@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sign-in{color:black;background-color:#3bbef1;font-size:16px;margin:10px;padding:5px 20px;border:2px solid;cursor:pointer;width:330px;border-radius:4px}.sign-up{color:black;background-color:#3bbef1;font-size:16px;margin:10px;padding:5px 20px;border:2px solid;cursor:pointer;width:330px;border-radius:4px}.one,.two{padding:0;border:none;background:none;color:#3bbef1;margin:15px;font-size:12px;cursor:pointer}.three{padding:0;border:none;background:none;color:#3bbef1;margin:15px;font-size:12px;cursor:pointer;position:relative;left:140px}

.image-background {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #f48fb2;
    position: relative;
    margin: 30px auto 0;
}

.names {
    display: flex;
    margin-bottom: 10px;
}
* {
  font-family: "Roboto", sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.copyright {
  color: rgb(177, 174, 174);
  font-size: 9px;
  margin: auto;
  width: 50%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
